
'use client';

import { useState, useEffect } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from '@/components/ui/card';
import { Checkbox } from '@/components/ui/checkbox';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Save } from 'lucide-react';
import type { Volunteer, UniversalRole } from '@/types';
import { Switch } from '@/components/ui/switch';
import { Separator } from '@/components/ui/separator';
import { Form, FormControl, FormField, FormItem, FormLabel } from '@/components/ui/form';
import { collection, doc, query, updateDoc } from 'firebase/firestore';
import { useAuth } from '@/hooks/use-auth';
import { useFirebase, useMemoFirebase } from '@/firebase';
import { useCollection } from '@/firebase/hooks/use-collection';
import { FirestorePermissionError } from '@/firebase/errors';
import { errorEmitter } from '@/firebase/error-emitter';

const preferencesSchema = z.object({
  preferredRoleIds: z.array(z.string()).default([]),
  servingFrequency: z.enum(['weekly', 'bi-weekly', 'monthly', 'as-needed']),
  familyServingPreference: z.enum(['only', 'possible', 'any']),
  familyManagementEnabled: z.boolean().default(false),
});

type PreferencesFormValues = z.infer<typeof preferencesSchema>;

interface AdminVolunteerPreferencesProps {
    volunteer: Volunteer;
}

export default function AdminVolunteerPreferences({ volunteer }: AdminVolunteerPreferencesProps) {
  const { churchId, user: adminUser } = useAuth();
  const { firestore } = useFirebase();
  const { toast } = useToast();
  const [isSaving, setIsSaving] = useState(false);

  const rolesQuery = useMemoFirebase(() => 
    firestore && churchId ? query(collection(firestore, 'churches', churchId, 'universalRoles')) : null,
    [firestore, churchId]
  );
  const { data: universalRoles = [] } = useCollection<UniversalRole>(rolesQuery);
  
  const form = useForm<PreferencesFormValues>({
    resolver: zodResolver(preferencesSchema),
    defaultValues: volunteer.preferences,
  });
  
  const familyManagementEnabled = form.watch('familyManagementEnabled');

  useEffect(() => {
    form.reset(volunteer.preferences);
  }, [volunteer, form]);

  const onSubmit = (data: PreferencesFormValues) => {
    if (!firestore || !churchId) return;
    setIsSaving(true);
    const userRef = doc(firestore, 'users', volunteer.id);

    updateDoc(userRef, { preferences: data })
      .then(() => {
        toast({
          title: 'Preferences Saved',
          description: `${volunteer.name}'s preferences have been updated.`,
        });
      })
      .catch(async (serverError) => {
        const permissionError = new FirestorePermissionError(adminUser, {
          path: userRef.path,
          operation: 'update',
          requestResourceData: { preferences: data },
        });
        errorEmitter.emit('permission-error', permissionError);
      })
      .finally(() => {
        setIsSaving(false);
      });
  };

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)}>
        <div className="space-y-6">
            <Card>
            <CardHeader>
                <CardTitle>Preferred Roles</CardTitle>
                <CardDescription>Select the roles this volunteer enjoys the most.</CardDescription>
            </CardHeader>
            <CardContent className="grid grid-cols-2 md:grid-cols-3 gap-4">
                <Controller
                control={form.control}
                name="preferredRoleIds"
                render={({ field }) => (
                    <>
                    {universalRoles.map(role => (
                        <div key={role.id} className="flex items-center space-x-2">
                        <Checkbox
                            id={`role-${role.id}`}
                            checked={field.value?.includes(role.id)}
                            onCheckedChange={(checked) => {
                            return checked
                                ? field.onChange([...(field.value || []), role.id])
                                : field.onChange(field.value?.filter(id => id !== role.id));
                            }}
                        />
                        <Label htmlFor={`role-${role.id}`} className="font-normal">{role.name}</Label>
                        </div>
                    ))}
                    </>
                )}
                />
            </CardContent>
            </Card>

            <Card>
            <CardHeader>
                <CardTitle>Serving Frequency</CardTitle>
                <CardDescription>How often would this volunteer ideally be scheduled?</CardDescription>
            </CardHeader>
            <CardContent>
                <FormField
                control={form.control}
                name="servingFrequency"
                render={({ field }) => (
                    <FormItem className="space-y-3">
                    <FormControl>
                        <RadioGroup
                        onValueChange={field.onChange}
                        value={field.value}
                        className="grid grid-cols-2 gap-4"
                        >
                        <FormItem className="flex items-center space-x-2">
                            <RadioGroupItem value="weekly" id="weekly" />
                            <Label htmlFor="weekly">Weekly</Label>
                        </FormItem>
                        <FormItem className="flex items-center space-x-2">
                            <RadioGroupItem value="bi-weekly" id="bi-weekly" />
                            <Label htmlFor="bi-weekly">Every 2 Weeks</Label>
                        </FormItem>
                        <FormItem className="flex items-center space-x-2">
                            <RadioGroupItem value="monthly" id="monthly" />
                            <Label htmlFor="monthly">Monthly</Label>
                        </FormItem>
                        <FormItem className="flex items-center space-x-2">
                            <RadioGroupItem value="as-needed" id="as-needed" />
                            <Label htmlFor="as-needed">As Needed</Label>
                        </FormItem>
                        </RadioGroup>
                    </FormControl>
                    </FormItem>
                )}
                />
            </CardContent>
            </Card>

            <Card>
            <CardHeader>
                <CardTitle>Family Management</CardTitle>
                <CardDescription>Coordinate scheduling with family members.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
                <FormField
                    control={form.control}
                    name="familyManagementEnabled"
                    render={({ field }) => (
                        <div className="flex items-center justify-between rounded-lg border p-4">
                            <div>
                                <h3 className="text-base font-medium">Enable Family Features</h3>
                                <p className="text-sm text-muted-foreground">
                                    Allows this volunteer to manage family members and availability.
                                </p>
                            </div>
                            <FormControl>
                                <Switch
                                checked={field.value}
                                onCheckedChange={field.onChange}
                                />
                            </FormControl>
                        </div>
                    )}
                    />
                
                {familyManagementEnabled && (
                <>
                    <Separator />
                    <FormField
                    control={form.control}
                    name="familyServingPreference"
                    render={({ field }) => (
                        <FormItem className="space-y-3">
                        <FormLabel>When serving, this volunteer prefers to serve:</FormLabel>
                        <FormControl>
                            <RadioGroup
                            onValueChange={field.onChange}
                            value={field.value}
                            className="space-y-2"
                            >
                            <FormItem className="flex items-center space-x-3">
                                <RadioGroupItem value="only" id="only" />
                                <Label htmlFor="only">Only with family</Label>
                            </FormItem>
                            <FormItem className="flex items-center space-x-3">
                                <RadioGroupItem value="possible" id="possible" />
                                <Label htmlFor="possible">With family whenever possible</Label>
                            </FormItem>
                                <FormItem className="flex items-center space-x-3">
                                <RadioGroupItem value="any" id="any" />
                                <Label htmlFor="any">Any time they are needed</Label>
                            </FormItem>
                            </RadioGroup>
                        </FormControl>
                        </FormItem>
                    )}
                    />
                </>
                )}

            </CardContent>
            </Card>

            <div className="flex justify-end">
                <Button type="submit" disabled={isSaving}>
                    {isSaving ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Save className="mr-2 h-4 w-4" />}
                    Save Preferences
                </Button>
            </div>
        </div>
      </form>
    </Form>
  );
}
