
'use client';

import { useMemo } from 'react';
import { notFound, useParams } from 'next/navigation';
import { Header } from '@/components/layout/Header';
import type { Volunteer, Family } from '@/types';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Loader2 } from 'lucide-react';
import AdminVolunteerPreferences from './_components/AdminVolunteerPreferences';
import AdminVolunteerAvailability from './_components/AdminVolunteerAvailability';
import AppShell from '@/components/layout/AppShell';
import { useAuth } from '@/hooks/use-auth';
import { useFirebase, useMemoFirebase } from '@/firebase';
import { doc } from 'firebase/firestore';
import { useDoc } from '@/firebase/hooks/use-doc';

export default function VolunteerDetailsPage() {
  const { churchId } = useAuth();
  const { firestore } = useFirebase();
  const params = useParams();
  const volunteerId = params.volunteerId as string;

  const volunteerDocRef = useMemoFirebase(() => 
    firestore && volunteerId ? doc(firestore, 'users', volunteerId) : null,
    [firestore, volunteerId]
  );
  const { data: volunteer, isLoading: isLoadingVolunteer } = useDoc<Volunteer>(volunteerDocRef);

  const familyDocRef = useMemoFirebase(() => 
    firestore && churchId && volunteer?.familyId ? doc(firestore, 'churches', churchId, 'families', volunteer.familyId) : null,
    [firestore, churchId, volunteer]
  );
  const { data: family, isLoading: isLoadingFamily } = useDoc<Family>(familyDocRef);
  
  const isLoading = isLoadingVolunteer || isLoadingFamily;

  if (isLoading) {
    return (
      <AppShell>
        <div className="flex min-h-screen w-full flex-col bg-muted/40">
          <Header />
          <main className="flex flex-1 items-center justify-center">
            <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
          </main>
        </div>
      </AppShell>
    );
  }

  if (!volunteer) {
    return notFound();
  }

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex-1 p-4 md:p-8">
          <div className="max-w-4xl mx-auto space-y-6">
            <div className="flex items-center gap-4">
              <Avatar className="h-16 w-16">
                <AvatarFallback>{volunteer.name.charAt(0)}</AvatarFallback>
              </Avatar>
              <div>
                <h1 className="text-3xl font-bold font-headline tracking-tight">{volunteer.name}</h1>
                <p className="text-muted-foreground">{volunteer.email}</p>
              </div>
            </div>
            <Tabs defaultValue="preferences">
              <TabsList>
                <TabsTrigger value="preferences">Preferences</TabsTrigger>
                <TabsTrigger value="availability">Availability</TabsTrigger>
              </TabsList>
              <TabsContent value="preferences" className="mt-4">
                  <AdminVolunteerPreferences volunteer={volunteer} />
              </TabsContent>
              <TabsContent value="availability" className="mt-4">
                  <AdminVolunteerAvailability volunteer={volunteer} family={family} />
              </TabsContent>
            </Tabs>
          </div>
        </main>
      </div>
    </AppShell>
  );
}
